export let initFormAddToCart = (form) => {
  if (!form) {
    return;
  }
  let btnAddToCart = form.querySelector(".btn-add-to-cart");

  form.addEvent("submit", (e) => {
    e.preventDefault();
    btnAddToCart.addClass("pending");
    Cart.add(form).finally(() => {
      btnAddToCart.removeClass("pending");
    });
  });
};
