export class Popup {
  constructor(container) {
    this.container = container;
    this.container.getElementsByClassName("js-modal-close").addEvents("click", this.close.bind(this));
  }

  close() {
    this.container.removeClass("is-open");
    AT.enableScroll();
  }

  open() {
    this.container.addClass("is-open");
    AT.disableScroll();
  }
}
